.navigation__inner-page {
  align-self: flex-end;
  width: min-content;
  position: relative;
  display: flex;
  justify-content: end;
  align-items: center;
  padding-left: 18px;
  font-family: 'Jost', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: var(--sub-text-color);
}
.navigation__inner-page::before {
  position: absolute;
  top: -1px;
  left: 0;
  color: var(--main-color);
  font-weight: 400;
  font-size: 18px;
  content: '>';
}
.main {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  grid-area: main;
  align-self: start;
  overflow: auto;
}
.main--index {
  background-image: url('../img/henkel-duesseldorf-logo.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
}
@media (max-width: 900px) {
  .main--index {
    background-position: 85% 0%;
  }
}
.footer {
  padding: 10px 20px;
  grid-area: footer;
  align-self: start;
}
.footer__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__wrap--contacts {
  justify-content: space-between;
}
.footer__wrap--contacts > .footer__suport {
  align-items: flex-end;
}
@media (max-width: 900px) {
  .footer__wrap--contacts {
    flex-direction: column;
    gap: 10px;
  }
  .footer__wrap--contacts > .footer__suport {
    align-items: center;
  }
}
.footer__suport {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Jost', 'Arial', sans-serif;
  font-weight: 300;
  font-size: 13px;
  color: var(--sub-text-color);
}
.footer__sup-link {
  color: var(--sub-text-color);
}
.footer__sup-link:hover,
.footer__sup-link:focus {
  color: var(--main-color);
}
.offer {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
}
.crumbs {
  width: 100%;
  padding: 10px 20px;
  z-index: 2;
}
.crumbs__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 5px;
  list-style: none;
}
.crumbs__wrap li:hover > .crumbs__crumb,
.crumbs__wrap li:focus-within > .crumbs__crumb {
  transition: all 0.1s linear;
  color: var(--main-color);
}
.crumbs__item {
  position: relative;
  padding-right: 14px;
}
.crumbs__item:not(:last-child):after {
  position: absolute;
  height: min-content;
  top: 0px;
  right: 0;
  color: var(--crumbs-arrow-color);
  font-weight: 400;
  font-size: 14px;
  content: '>';
}
@media (min-width: 900px) {
  .crumbs__item:hover .crumbs__dropdown,
  .crumbs__item:focus-within .crumbs__dropdown {
    opacity: 1;
    pointer-events: all;
  }
}
.crumbs__crumb {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  transition: all 0.1s linear;
  text-decoration: none;
}
.crumbs__crumb--dropdown-arrow {
  padding-right: 12px;
  cursor: pointer;
}
.crumbs__crumb--dropdown-arrow:after {
  content: '';
  position: absolute;
  top: 40%;
  right: 0;
  border-top: 8px solid var(--sub-text-color);
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
}
@media (max-width: 900px) {
  .crumbs__crumb--dropdown-arrow {
    padding-right: 0;
  }
  .crumbs__crumb--dropdown-arrow:after {
    display: none;
  }
}
.crumbs__dropdown {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  min-width: 120px;
  padding: 10px;
  list-style: none;
  background-color: var(--back-color);
  box-shadow: var(--shadow);
  box-shadow: var(--inner-shadow);
  z-index: 1;
  border-radius: 4px;
}
.crumbs__droped-link {
  padding: 4px;
  text-decoration: none;
  display: flex;
  justify-content: start;
  transition: all 0.1s linear;
}
.crumbs__text {
  font-family: 'Jost', 'Arial', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: var(--sub-text-color);
  user-select: none;
  white-space: nowrap;
}
.crumbs__text--active {
  color: var(--standart-text-color);
  cursor: default;
}
.crumbs__text:not(.crumbs__text--active):hover,
.crumbs__text:not(.crumbs__text--active):focus {
  color: var(--main-color);
  transition: all 0.1s linear;
}
.inner-data {
  display: grid;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 10px;
  padding: 10px;
  height: 100%;
  box-shadow: var(--inner-shadow);
  background-color: var(--data-list-color);
}
.inner-data__title {
  width: 100%;
  text-align: left;
  font-family: 'Jost', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 22px;
  color: var(--standart-text-color);
}
.inner-data__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: min-content;
  grid-gap: 10px;
  border-radius: 15px;
  list-style: none;
}
@media (min-width: 1200px) {
  .inner-data__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 900px) {
  .inner-data__list {
    grid-template-columns: 1fr;
  }
}
.inner-data__item {
  border-radius: 10px;
  padding: 30px 0;
  min-width: min-content;
}
.inner-data__item:nth-child(6n - 6) {
  background-color: var(--inner-data-n1-color);
}
.inner-data__item:nth-child(6n - 5) {
  background-color: var(--inner-data-n2-color);
}
.inner-data__item:nth-child(6n - 4) {
  background-color: var(--inner-data-n3-color);
}
.inner-data__item:nth-child(6n - 3) {
  background-color: var(--inner-data-n4-color);
}
.inner-data__item:nth-child(6n - 2) {
  background-color: var(--inner-data-n5-color);
}
.inner-data__item:nth-child(6n - 1) {
  background-color: var(--inner-data-n6-color);
}
.inner-data__inner-list {
  list-style: none;
}
.inner-data__inner-item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;
  align-items: center;
  padding: 5px 10px;
  border-bottom: 1px solid var(--inner-data-text-color);
}
.inner-data__text {
  font-family: 'Jost', 'Arial', sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: var(--inner-data-text-color);
}
@media (max-width: 600px) {
  .inner-data__text {
    font-size: 18px;
  }
}
.inner-data__item-name {
  font-weight: 500;
  text-align: left;
}
.inner-data__item-desc {
  text-align: right;
}
@media (max-width: 900px) {
  .inner-data__item-desc {
    text-align: center;
  }
}
.profil {
  max-height: 100%;
}
.profil__wrap {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  max-height: 100%;
}
.profil__image {
  z-index: 0;
  width: 100%;
  height: 250px;
  background-image: url('../img/profil.jpg');
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  background-attachment: fixed;
}
@media (max-width: 900px) {
  .profil__image {
    height: 200px;
  }
}
@media (max-width: 600px) {
  .profil__image {
    height: 150px;
  }
}
.profil__data {
  position: relative;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  padding-top: calc(200px / 2 + 10px);
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 2000px) {
  .profil__data {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  .profil__data > .profil__block {
    width: max-content;
    min-width: 400px;
  }
}
@media (max-width: 1200px) {
  .profil__data {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 900px) {
  .profil__data {
    grid-template-columns: 1fr;
    padding-top: calc(150px / 2 + 10px);
    padding-left: 10px;
    padding-right: 10px;
  }
}
@media (max-width: 600px) {
  .profil__data {
    padding-top: calc(100px / 2 + 10px);
  }
}
.profil__person-image {
  position: absolute;
  top: calc(-200px / 2);
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid var(--back-color);
  box-shadow: -10px 10px 20px rgba(0, 0, 0, 0.4);
}
@media (max-width: 900px) {
  .profil__person-image {
    height: 150px;
    width: 150px;
    top: calc(-150px / 2);
  }
}
@media (max-width: 600px) {
  .profil__person-image {
    height: 100px;
    width: 100px;
    top: calc(-100px / 2);
  }
}
.profil__block {
  width: 100%;
  background-color: var(--back-color);
  box-shadow: var(--shadow);
  padding: 20px;
  border-radius: 20px;
}
@media (max-width: 900px) {
  .profil__block {
    padding: 20px 15px;
  }
}
.profil__section-title {
  font-size: 20px;
  color: var(--sub-text-color);
  font-weight: 500;
  text-align: center;
}
.profil__list-item {
  position: relative;
  display: grid;
  grid-template-columns: minmax(min-content, max-content) 1fr minmax(50px, max-content);
  grid-template-rows: 1fr;
  grid-template-areas: 'title line description';
  grid-gap: 10px;
  padding-top: 5px;
  border-bottom: 1px solid var(--scrollbar-thumb-color);
}
.profil__list-item::after {
  content: '';
  grid-area: line;
  align-self: center;
  width: 100%;
  height: 2px;
  border-top: 2px dashed var(--crumbs-arrow-color);
  top: 50%;
  transform: translateY(50%);
  left: 0;
}
.profil__title {
  grid-area: title;
  position: relative;
  font-size: 18px;
  color: var(--standart-text-color);
  font-weight: 400;
  text-align: left;
}
.profil__title::after {
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: tomato;
}
.profil__description {
  word-break: break-all;
  grid-area: description;
  font-size: 18px;
  color: var(--standart-text-color);
  font-weight: 400;
  text-align: right;
}
.info-table {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-row-gap: 10px;
  height: 100%;
}
.info-table__title-wrap {
  padding: 0 20px;
}
.info-table__title {
  width: 100%;
  font-family: 'Jost', 'Arial', sans-serif;
  font-weight: 500;
  font-size: 22px;
  color: var(--sub-text-color);
}
.info-table__wrap {
  max-height: 100%;
  overflow: auto;
}
.info-table__table {
  position: relative;
  width: 100%;
  min-width: 100%;
  border-spacing: 0;
  text-align: center;
  margin: 0;
  padding: 0;
  overflow: auto;
  table-layout: fixed;
  border-collapse: collapse;
  color: var(--standart-text-color);
}
.info-table__table thead {
  z-index: 1;
  position: sticky;
  top: 0;
  background-color: var(--back-color);
  box-shadow: var(--main-table-head-shadow);
}
.info-table__table th {
  width: 120px;
  padding: 7px;
  font-weight: 500;
  font-size: 17px;
  opacity: 1;
}
.info-table__table > tbody > tr > td {
  padding: 10px;
  font-size: 14px;
  position: relative;
  cursor: pointer;
}
.info-table__table > tbody > tr > td > a {
  text-decoration: none;
  color: var(--standart-text-color);
  padding: 10px;
  font-size: 14px;
  position: relative;
  cursor: pointer;
}
.info-table__table > tbody > tr > td:hover > a,
.info-table__table > tbody > tr > td:focus > a {
  color: var(--main-color);
}
.info-table__table > tbody > tr:hover {
  padding: 20px;
  box-shadow: var(--main-table-shadow);
}
.info-table__table td:hover::after {
  content: "";
  position: absolute;
  box-shadow: var(--main-table-shadow);
  left: 0;
  top: 0;
  height: 100%;
  border-radius: 8px;
  width: 100%;
  z-index: -1;
}
.contacts-list {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 20px;
}
.contacts-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  box-shadow: var(--shadow);
  background: var(--back-color);
  cursor: pointer;
}
.contacts-list__item:hover {
  box-shadow: 0px 0px 10px 6px rgba(0, 0, 0, 0.1);
  background: var(--hover-text-color);
}
.contacts-list__link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: var(--standart-text-color);
  font-size: 25px;
}
.contacts-list__link:hover {
  color: #ffffff;
}
.contacts-list__link--facebook:hover {
  background: #3b5998;
}
.contacts-list__link--twitter:hover {
  background: #1da1f2;
}
.contacts-list__link--instagram:hover {
  border-radius: 50%;
  background: linear-gradient(135deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
}
:root {
  --fg: #17181c;
  --shade1: #727274;
  --shade2: #cccdd1;
  --shade3: #f3f4f8;
  --shade4: #ffffff;
  --dur1: 1s;
  --dur2: 6s;
  font-size: calc(16px + (32 - 16)*(100vw - 320px)/(2560 - 320));
}
.preloader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-image: linear-gradient(145deg, var(--shade3), var(--shade2));
  color: var(--fg);
  display: flex;
  font: 1em/1.5 Hind, sans-serif;
  flex-direction: column;
  height: 100vh;
  margin: auto;
  transition: all 0.4s ease;
  opacity: 0;
  pointer-events: none;
}
.preloader--load {
  opacity: 1;
  pointer-events: all;
}
.preloader__wrap {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.preloader__load {
  animation: largePopOut var(--dur1) linear;
  border-radius: 50%;
  box-shadow: var(--shadow);
  margin-bottom: 20px;
  position: relative;
  width: 12em;
  height: 12em;
}
.preloader__square {
  animation: smallPopOut1 var(--dur1) linear, popInOut var(--dur2) var(--dur1) linear infinite;
  border-radius: 0.5em;
  box-shadow: var(--inner-shadow);
  position: absolute;
  top: 2.5em;
  left: 2.5em;
  width: 3em;
  height: 3em;
}
.preloader__square:nth-child(n + 2):nth-child(-n + 3) {
  left: 6.5em;
}
.preloader__square:nth-child(n + 3) {
  top: 6.5em;
}
.preloader__square:nth-child(2) {
  animation: smallPopOut2 var(--dur1) linear, move2 var(--dur2) var(--dur1) linear infinite;
}
.preloader__square:nth-child(3) {
  animation: smallPopOut3 var(--dur1) linear, move3 var(--dur2) var(--dur1) linear infinite;
}
.preloader__square:nth-child(4) {
  animation: smallPopOut4 var(--dur1) linear, move4 var(--dur2) var(--dur1) linear infinite;
}
.status {
  animation: fadeIn var(--dur1) linear forwards;
  text-align: center;
}
.status__dot {
  animation: appear1 var(--dur1) var(--dur1) steps(1, start) infinite;
  display: inline-block;
}
.status__dot:nth-child(2) {
  animation: appear2 var(--dur1) var(--dur1) steps(1, start) infinite;
}
.status__dot:nth-child(3) {
  animation: appear3 var(--dur1) var(--dur1) steps(1, start) infinite;
}
/* Animations */
@keyframes largePopOut {
  from,
  20% {
    box-shadow: 0 0 0 var(--shade4) inset, 0 0 0 var(--shade1) inset, 0 0 0 var(--shade1), 0 0 0 var(--shade4);
  }
  40% {
    box-shadow: 0.15em 0.15em 0.15em var(--shade4) inset, -0.15em -0.15em 0.15em var(--shade1) inset, 2em 2em 2em var(--shade1), -2em -2em 4em var(--shade4);
  }
  60%,
  to {
    box-shadow: 0.15em 0.15em 0.15em var(--shade4) inset, -0.15em -0.15em 0.15em var(--shade1) inset, 1em 1em 2em var(--shade1), -1em -1em 2em var(--shade4);
  }
}
